<template>
  <div class="center">
    <Card style="width:320px">
        <div style="text-align:center">
          <img src="./../assets/img/avatar.png" alt="Licoy">
          <br><br>
            <h3>憧憬Licoy / Licoy</h3>
            <br>
            <div>
              <template v-for="(item,index) in urlList">
                <Tooltip :content="item.name" :key="index" placement="bottom">
                  <IButton type="primary" @click="send(index)" shape="circle" :icon="item.icon"></IButton>
                </Tooltip>
              </template>
            </div>
        </div>
    </Card>
  </div>
</template>

<script>
import Card from 'iview/src/components/card';
import Tooltip from 'iview/src/components/tooltip';
import IButton from 'iview/src/components/button';
export default {
  components:{
      Card,Tooltip,IButton
  },
  data () {
    return {
      urlList:[
        {
          name:'Github',
          url:'https://github.com/Licoy',
          icon:'social-github'
        }
      ]
    }
  },
  methods:{
    send(index){
      window.location = this.urlList[index].url
    }
  }
}
</script>

<style scoped>
.center{
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  overflow: auto;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
</style>



